import { inject, observer } from "mobx-react"
import DeriNumberFormat from "../../../../utils/DeriNumberFormat";
import classNames from "classnames";
import './position.less'
import ApiProxy from "../../../../model/ApiProxy";
import {useAlert} from 'react-alert'
import UnderlineText from "../../../../components/UnderlineText/UnderlineText";
import Type from "../../../../model/Type";
import { BUY } from "../../../../utils/Constants";
import styled from "styled-components";

const OptionLiqPrice = styled.div`
  display : flex;
  align-item : center;
`
const LiqText = ({liqDetail, index,lang}) => {
  const tipKey = liqDetail.numPositions > 1 ? 'liq-price-hover-three' : 'liq-price-hover-two'
  return(
    <OptionLiqPrice key={index}>
      <div>{liqDetail.underlier}</div>
      <div>{liqDetail.price1 ? liqDetail.price1 : <UnderlineText text='?' multiline ={true} tip={lang[tipKey]} width='auto'/>}</div>
      <div>/</div>
      <div>{liqDetail.price2 ? liqDetail.price2 : <UnderlineText text='?' multiline ={true} tip={lang[tipKey]} width='auto'/>}</div>
    </OptionLiqPrice>
  )
}

const LiqPriceDisplay = ({pos,lang}) => {
  const liqPrices = pos.liquidationPrice;
  return (
    Array.isArray(liqPrices) 
    ? 
      liqPrices.map((detail,index) => <LiqText liqDetail={detail} index={index} key={index} lang={lang}/>)
    : <DeriNumberFormat value={liqPrices} decimalScale={4} />
  )
}

const formatNumber = (number,decimal = 2,suffix = '',prefix ='') => {
  return <DeriNumberFormat value={number} decimalScale={decimal} prefix={prefix} suffix={suffix}/>
}

function Position({wallet,lang,trading}){
  const alert = useAlert();
  const columns = {
    symbol : {text : lang['symbol']},
    direction : {text : lang['direction'],className : pos => pos.direction === BUY ? 'green' : 'red'},
    position : {text : lang['position'],value : pos => <><div>{formatNumber(pos['volume'],trading.minTradeScaleDecimal,trading.symbolInfo.unit)}</div><div className='gray'>{formatNumber(pos['notional'],2)}</div></>},
    averageEntryPrice : {text : lang['entry-price'],value : pos => formatNumber(pos['averageEntryPrice'],6)},
    markPrice : {text : lang['mark-price'],value : pos => formatNumber(pos['markPrice'])},
    marginHeldBySymbol : {text : lang['margin-usage'],value : pos => formatNumber(pos['marginHeldBySymbol'])},
    liquidationPrice : {text : lang['liq-Price'], tip : Type.isOption && lang["option-liq-price-tip"],value : pos => <LiqPriceDisplay pos={pos} lang={lang}/>},
    unrealizedPnl : {text : lang['unrealizedPnl'],value : pos => <><div className={pos['unrealizedPnl'] > 0 ? 'green' : 'red'}>{formatNumber(pos['unrealizedPnl'])}</div><div className='gray'>{formatNumber(pos['unrealizedPnlRate'])}</div></>},
    fundingFee : {text : lang['fundingFee'],className : pos => pos['fundingFee'] < 0 ? 'green' : 'red',tip: lang['funding-fee-tip'],value : pos => Type.isFuture ? formatNumber((-pos['fundingFee']),8) : formatNumber((-pos['premiumFundingAccrued']),8)},
    action : {text : lang['action'],value : lang['close'],className : 'close-position' ,onClick : pos => closePosition(pos.symbolId)}
  }
  const keys = Object.keys(columns)
  
  const closePosition = async symbolId => {
    const res = await ApiProxy.request('closePosition',
      [wallet.detail.chainId, trading.pool.address, wallet.detail.account, symbolId],
      {includeResponse : true})
    if(res.success){
      alert.success(lang['close-position-success']);
      trading.refresh();
    } else {
      alert.error(lang['close-position-failed'])
    }
  }
  return (
    <div className='m-position'>
      <div className='p-header'>
        {keys.map(key => {
          return (
            <div key={key} className ='p-col'>
              {columns[key].tip 
              ? 
                <UnderlineText tip={columns[key].tip} text={columns[key].text} multiline width='auto'/> 
              :
                columns[key].text
              }
            </div>
          )
        })}
      </div>
      <div className='p-body'>
        {trading.positions.map((position,i) => (
          <div className='p-row' key={i}>
            {keys.map(key => {
              const col = columns[key]
              const colClass = classNames('p-col',typeof col.className === 'function' ? col.className(position) : col.className)
              return (
                <div className={colClass} key={key} onClick={() => typeof col.onClick === 'function' && col.onClick(position)}>
                  {typeof col.value === 'function' ? col.value(position) : (position[key] || columns[key].value)}
                </div>
              )
            })}
          </div>
        ))}
        
      </div>
    </div>
  )
}

const DisplayColumn = ({col,data}) => {
  return (
    <>
      {col.type === 'number' 
        ? <DeriNumberFormat prefix={col.prefix} value={data[col.key]} suffix={col.suffix} decimalScale={col.decimal || 2}/> 
        : data[col.key]}
    </>
    )
}

export default inject('trading','wallet')(observer(Position))