import React from "react";
import { Pie, PieChart, Cell,Label ,Tooltip} from "recharts";
import styled from "styled-components";
import UnderlineText from "../UnderlineText/UnderlineText";

const Wrapper = styled.div`
  height: 100%;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size : 24px;
  color : #E0ECFF;
  .unit {
    font-size: 12px;
    display: flex;
    margin-left: 2px;
    height: 24px;
    align-items: flex-end;
  }
  .text {
    display : flex;
  }
`

const CustomizedLabel = ({cx,cy,midAngle,innerRadius,outerRadius,value,index}) => {
  const RADIAN = Math.PI / 180;
  const radius = 5+ innerRadius + (outerRadius - innerRadius);
  let x = cx + radius * Math.cos(-midAngle * RADIAN);
  let y = cy + radius * Math.sin(-midAngle * RADIAN);
  if(index === 0){
    x = 60;
    y = 135
  } else if (index === 2){
    x = 155;
    y = 135
  }
  return (
    <text x={x} y={y} fill="#93A1B8"
      textAnchor={x > cx ? "start" : "end"}
      dominantBaseline="central"
      fontSize='12px'
      width='100'
    >
      {perentage[index]}
    </text>
  );
}
const CustomTooltip = ({ value,tip }) => {
  return (
    <Wrapper>
      {value &&<UnderlineText tip={tip} multiline={true} width='375'>{value}<div className='unit'> %</div></UnderlineText>}
    </Wrapper>
  );
};
const perentage = ['0%','100%','200%']
export default function Gauge({accountInfo = {},lang}){
  const data = [{ value: (+accountInfo.marginUsage || 0).toFixed(2) }, { value: (+accountInfo.dynEffMargin || 0).toFixed(2) },{value : (+accountInfo.dynEffMargin || 0).toFixed(2)}].map(d => ({value : +d.value}));
  const marginUsagePercent = isNaN(accountInfo.marginUsagePercent) ? '' : accountInfo.marginUsagePercent
  const tipContent = `${marginUsagePercent}% : ${lang['graph-current-margin-usage-tip']}`
  return (
    <PieChart height={150} width={200}>
      <defs>
        <linearGradient
          id="usageMargin"
          gradientTransform="rotate(23deg)"
        >
          <stop offset="13.8%" stopColor="#8EEC6D" />
          <stop offset="87.76%" stopColor="#FF8C39" />
        </linearGradient>

        <linearGradient
          id="availMargin"
          gradientTransform="rotate(90deg)"
        >
          <stop offset="0.17%" stopColor="#43608D" />
          <stop offset="100.17%" stopColor="#3F4D8B" />
        </linearGradient>
      </defs>
      <Pie
        cy='55%'
        cx='55%'
        startAngle={230}
        endAngle={-50}
        innerRadius={'60%'}
        outerRadius={'80%'} 
        data={data}
        dataKey="value"
        labelLine={false}
        cornerRadius={8}
        stroke="none"
        isAnimationActive={false}
        label={<CustomizedLabel/>}
      >
        <Cell fill="url(#usageMargin)"  />
        <Cell fill="url(#availMargin)" fillOpacity='0.4' />
        <Cell fill="#CE5E5E" fillOpacity='0.15'/>
      </Pie>
        <Tooltip cy='70%' content={<CustomTooltip tip={tipContent} value ={marginUsagePercent}/>} 
          wrapperStyle={{ visibility: "visible", pointerEvents: "auto",
          width : '100%',
          height : '100%',
          left: '5px',
          top: '0px' }}/>
        {/* <Cell component={<CustomTooltip value='0%'/>}/> */}
    </PieChart>
  )
};